import skeleton from '../../components/skeleton/index.vue'
import mkbType from '../../components/mkb-type/mkb-type.vue'
import mkbState from '../../components/mkb-state/mkb-state.vue'
import mkbSearchItem from '../../components/mkb-search-item/mkb-search-item.vue'
import Messages from '../../util/messages.js'
import Constant from '../../util/constant.js'
import uniSearchBar from '../../components/uni-search-bar/uni-search-bar.vue'
import mkbImgCut from '../../components/mkb-img-cut/mkb-img-cut.vue'
export default {
	components: {
		skeleton,
		mkbType,
		mkbState,
		mkbSearchItem,
		uniSearchBar,
		mkbImgCut
	},
	data() {
		return {
			showSkeleton: true,
			//加载骨架
			skeleton: {
				row: 4,
				imgTitle: true,
				showAvatar: false,
				showTitle: false,
				category: 8
			},
			//广告滑动
			swiperItem: {
				indicatorDots: true,
				activteColor: 'rgba(255,255,255,1)',
				color: 'rgba(255,255,255,0.4)',
			},
			bannerList: [{
					bannerId: '23232',
					image: 'http://testqiniu.unitelife.cn/sys/category/73-1576205109091.jpeg'
				},
				{
					bannerId: '9898',
					image: 'https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1208538952,1443328523&fm=26&gp=0.jpg'
				}
			],
			//搜索
			searchBar: {
				background: "rgba(0,0,0,0.05)",
				placeholder: "搜索",
				borderRadius: '90.57rpx',
				readonly: true
			},
			homeIcons:Constant.defaultHomeImgs,
			items: [],
			//数据列表
			list: []
		}
	},
	computed: {
		computedCategory() {
			this.renderTypes();
		}
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
		}
		return {
			title: '找装修',
			path: '/pages/index/index',
			desc: '随时随地享受成都生活'
		}
	},
	onShow() {





		// this.getCategory();
	},
	onLoad() {

		setTimeout(() => {
			this.loadHostCase();
			this.getCategory();
			this.showSkeleton = false;
		}, 2000);


	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
		this.loadHostCase();
		this.getCategory();
	},

	methods: {

		/**
		 * 获取categorys
		 */
		getCategory() {
			if (!uni.getStorageSync("catalogues")) {
				this.$http.getCategory({}).then(res => {
					this.$store.commit("setCatalogues",res.data);
				});
			}
		},


		//渲染decoration types
		renderTypes() {
			let categorys;
			if (uni.getStorageSync("catalogues")) {
				categorys = uni.getStorageSync("catalogues").categorys;
			} else if (this.$store.state.catalogues) {
				categorys = this.$store.state.catalogues.categorys;
			}

			if (categorys && categorys.length > 0) {
				let items = categorys.filter((item) => {
					return item.type == 1;
				});
				while (items.length < 10) {
					items.push({
						id: 10,
						decorateStyle: "预算有限",
						iconImg: '../../static/search/youxian.png',
						type: 1
					});
				}
				this.items = items;
			}
		},

		/**
		 * 根据类型查询
		 * @param {Object} item
		 */
		search(item) {
			//存储当前的选中装修类型
			uni.setStorageSync("itemType", item);
			this.$openPage({
				name: 'search'
			});
		},

		turnPrice(type) {
			if (type == 1) {
				uni.setStorageSync('priceList', []);
				this.$openPage({
					name: 'area',
					query: {
						type: type
					}
				})
			} else {
				uni.setStorageSync('designList', []);
				this.$openPage({
					name: 'manner',
					query: {
						type: type
					}
				})
			}
		},
		/**
		 * 拉取热门案例
		 */
		loadHostCase() {
			let that = this;
			uni.showLoading();
			that.$http.getHotCase({}).then(res => {
				uni.hideLoading();
				if (res && res.code == 200) {
					if (res.data && res.data.length > 0) {
						res.data.forEach(item => {
							try {
								if (item.coverImg) {
									let imgs = JSON.parse(item.coverImg);
									item.coverImg = imgs && imgs.length > 0 ? imgs : [Constant.defaultImage];
								} else {
									item.coverImg = [Constant.defaultImage];
								}
							} catch (e) {
								item.coverImg = [Constant.defaultImage];
							}
						})
						that.list = res.data;
					}
				} else {
					this.showToast({
						icon: "none",
						title: res.msg || Messages.FAIL_INFO
					})
				}
			})
		},

		/**
		 * 跳转到详情页面
		 * @param {Object} id 报价id
		 */
		toDetail(quoteId) {
			// uni.navigateTo({
			// 	url:`../example/example?id=${id}`
			// })

			this.$openPage({
				name: 'example',
				query: {
					quoteId
				}
			})
		}

	}
}
